import { connect } from 'react-redux';
import RetailProfileAndSettingsComponent from './RetailProfileAndSettingsComponent';
import { goToProfileFormStep } from '../../redux/actions/userProfileFormActions';
import { goToResetPasswordFormStep } from '../../redux/actions/userActions';
import { goToRegionFormStep } from '../../redux/actions/regionFormActions';

const mapStateToProps = state => {
  const { form } = state;

  return {
    profileForm: form.userProfileForm || { values: {} },
    regionForm: form.userProfileRegion || { values: {} },
    resetPasswordRequest: form.resetPasswordRequest || { values: {} }
  };
};
const mapDispatchToProps = {
  goToProfileFormStep,
  goToRegionFormStep,
  goToResetPasswordFormStep
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailProfileAndSettingsComponent);



