import { connect } from 'react-redux';
import { getFormValues, reduxForm } from 'redux-form';
import ThemeSettingsComponent from './ThemeSettingsComponent';
import config from '../../../config';
import { setUserConfig } from '../../../redux/actions/userActions';

const ThemeForm = reduxForm({
  form: 'themeForm',
  enableReinitialize: true,
  onSubmit: () => {}
})(ThemeSettingsComponent);

const mapStateToProps = state => {
  const { theme } = config.global;
  const localConfig = JSON.parse(localStorage.getItem('apexTemplate')) || {};
  const { UserId, UserName } = state.user.userInfo;
  const userConfigTheme = (
    state.user.userConfig.find(c => c.Key === 'theme') || {}
  ).Value;
  return {
    UserId,
    UserName,
    initialValues: {
      theme: userConfigTheme || localConfig.theme || theme
    },
    formValues: getFormValues('themeForm')(state) || {}
  };
};

const mapDispatchToProps = dispatch => ({
  setUserConfig: ({ UserId, UserName, config }) =>
    dispatch(setUserConfig(UserId, UserName, config))
});

const ThemeSettingsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(ThemeForm);

export default ThemeSettingsContainer;



