import { connect } from 'react-redux';
import { reduxForm, change } from 'redux-form';
import Enable2FAModalComponent from './Enable2FAModal';
import modalDecorator from '../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../redux/actions/modalActions';
import { enable2FA } from '../../redux/actions/userActions';

const mapStateToProps = state => ({
  GoogleQRCode: state.user.qrCode.ManualCode,
  userName: state.user.userInfo.UserName
});

const mapDispatchToProps = {
  changeFieldValue: change
};

const containerForm = reduxForm({
  form: 'enable2FAModal',
  onSubmit: (payload, dispatch, props) => dispatch(enable2FA(payload.code)),
  onChange: (values, dispatch, props) => {
    if (values.code && values.code.length === 6) {
      props.submit();
    }
  }
})(Enable2FAModalComponent);

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(containerForm);

export default modalDecorator({
  name: MODAL_TYPES.ENABLE_2FA
})(container);



