import { change } from 'redux-form';
import { changeLanguage } from './languageActions';
import { setUserConfig } from './userActions';

export const goToRegionFormStep = step => dispatch =>
  dispatch(change('userProfileRegion', 'step', step));

export const setUserRegionConfig = () => (dispatch, getState) => {
  const {
    form: { userProfileRegion },
    user: { userInfo }
  } = getState();
  const { values } = userProfileRegion;
  const { UserId, UserName } = userInfo;
  const configValue = [{ Key: 'BaseCurrency', Value: values.baseCurrency }];

  dispatch(setUserConfig(UserId, UserName, configValue));
  dispatch(changeLanguage(values.language));
};



