import React from 'react';
import PropTypes from 'prop-types';
import QRCode from 'qrcode.react';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import './APQRCode.css';

const APQRCode = props => {
  const { value, customClass, size } = props;
  const baseClass = 'qr-code';
  const bemClasses = getBEMClasses(baseClass, customClass);

  return <QRCode value={value} size={size} className={bemClasses()} />;
};

APQRCode.defaultProps = {
  size: 160,
  customClass: ''
};

APQRCode.protoTypes = {
  size: PropTypes.number,
  customClass: PropTypes.string,
  vakue: PropTypes.string.isRequired
};

export default APQRCode;



