import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../common/APButton';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import ContentWithFooterLayoutTemplate from '../../common/LayoutTemplates/ContentWithFooterLayoutTemplate/ContentWithFooterLayoutTemplate';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RegionConfirmComponent.css';

const baseClass = 'region-confirm';
const regionConfirmClasses = getBEMClasses(baseClass);

const RegionConfirm = (props, context) => {
  const { formObj, languageNames, doSubmit } = props;

  const footer = (
    <APButton
      customClass={regionConfirmClasses()}
      onClick={() => doSubmit(formObj)}>
      {context.t('Update')}
    </APButton>
  );

  return (
    <div className={regionConfirmClasses()}>
      <ContentWithFooterLayoutTemplate
        footer={footer}
        customClass={`${baseClass}-cwf-lt`}>
        <React.Fragment>
          <div className={regionConfirmClasses('confirm-block')}>
            {context.t('Confirm')}
          </div>

          <APLabelWithText
            label={context.t('Language')}
            text={languageNames[formObj.language]}
            customClass={regionConfirmClasses()}
          />
          <APLabelWithText
            label={context.t('Display Currency')}
            text={formObj.baseCurrency}
            customClass={regionConfirmClasses()}
          />
        </React.Fragment>
      </ContentWithFooterLayoutTemplate>
    </div>
  );
};

RegionConfirm.propTypes = {
  formObj: PropTypes.object.isRequired
};

RegionConfirm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RegionConfirm;



