import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import RegionSettingsComponent from './RegionSettingsComponent';
import { setUserRegionConfig } from '../../../redux/actions/regionFormActions';
import { isRegionSectionEditingDisabled } from '../../../redux/selectors/userSelectors';
import config from '../../../config';
import { productOptionsSelector } from '../../../redux/selectors/baseCurrenciesSelectors';

const RetailRegionForm = reduxForm({
  form: 'userProfileRegion',
  enableReinitialize: true,
  onSubmit: (payload, dispatch) => {
    debugger;
    return dispatch(setUserRegionConfig(payload));
  }
})(RegionSettingsComponent);

const mapStateToProps = state => {
  const {
    form,
    user: { userConfig }
  } = state;
  const baseCurrency = (
    userConfig.find(config => config.Key === 'BaseCurrency') || {}
  ).Value;
  const { productsOptions, isMisconfigured } = productOptionsSelector(state);
  const editIsDisabled = isRegionSectionEditingDisabled(state);
  const { languageNames } = config.Language;
  return {
    initialValues: {
      language: state.i18nState.lang,
      baseCurrency: baseCurrency
    },
    formObj: form.userProfileRegion ? form.userProfileRegion.values : {},
    translations: state.i18nState.translations,
    languageNames,
    editIsDisabled,
    baseCurrency,
    productsOptions,
    isMisconfigured
  };
};

const mapDispatchToProps = dispatch => ({
  doSubmit: () => dispatch(setUserRegionConfig())
});

const RegionSettingsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailRegionForm);

export default RegionSettingsContainer;



