import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../common/APButton';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import ContentWithFooterLayoutTemplate from '../../common/LayoutTemplates/ContentWithFooterLayoutTemplate/ContentWithFooterLayoutTemplate';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './ThemeConfirmComponent.css';

const baseClasses = getBEMClasses('theme-confirm');

class ThemeConfirm extends React.Component {
  update = () => {
    this.props.doSubmit();
    this.props.goToStep(0);
  };

  render() {
    const { formValues } = this.props;

    const footer = (
      <APButton customClass={baseClasses()} onClick={this.update} type="button">
        {this.context.t('Update')}
      </APButton>
    );

    return (
      <div className={baseClasses()}>
        <ContentWithFooterLayoutTemplate
          footer={footer}
          customClass={`${baseClasses()}-cwf-lt`}>
          <React.Fragment>
            <div className={baseClasses('confirm-block')}>
              {this.context.t('Confirm')}
            </div>

            <APLabelWithText
              label={this.context.t('Color Theme')}
              text={formValues.theme}
              customClass={baseClasses()}
            />
          </React.Fragment>
        </ContentWithFooterLayoutTemplate>
      </div>
    );
  }
}

ThemeConfirm.propTypes = {
  formValues: PropTypes.object.isRequired
};

ThemeConfirm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ThemeConfirm;



