import { createSelector } from 'reselect';

export const isStatusLevelIncreased = createSelector(
  state => state.user,
  user => {
    const { userConfig, selectedAccountId, accounts } = user;

    const areAccountsReady = !!selectedAccountId && accounts.length > 0;
    if (!areAccountsReady) return;

    const verificationLevel = accounts.find(
      account => account.AccountId === selectedAccountId
    ).VerificationLevel;

    const levelIncreaseStatus = userConfig.some(
      configOption => configOption.Key === 'levelIncreaseStatus'
    );

    return (
      verificationLevel > 0 || (levelIncreaseStatus && verificationLevel === 0)
    );
  }
);

export const isProfileSectionEditingDisabled = isStatusLevelIncreased;
export const isRegionSectionEditingDisabled = isStatusLevelIncreased;



