import React from 'react';
import PropTypes from 'prop-types';
import APSelect from '../../common/APSelect/APSelect';
import APButton from '../../common/APButton';
import ContentWithFooterLayoutTemplate from '../../common/LayoutTemplates/ContentWithFooterLayoutTemplate/ContentWithFooterLayoutTemplate';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './ThemeFormComponent.css';

const baseClasses = getBEMClasses('theme-form');

class ThemeFormEditable extends React.Component {
  componentDidMount() {
    const { formValues, change } = this.props;
    const themeOptions = this.getThemeOptions();

    if (!formValues['theme']) {
      const { value } = themeOptions[0];
      change('theme', value);
    }
  }

  getThemeOptions = () => [
    { value: 'dark', label: 'Dark' },
    { value: 'light', label: 'Light' }
  ];

  render() {
    const { goToNextStep } = this.props;

    const footer = (
      <APButton customClass={baseClasses()} onClick={goToNextStep}>
        {this.context.t('Next')}
      </APButton>
    );

    return (
      <div className={baseClasses()}>
        <ContentWithFooterLayoutTemplate
          footer={footer}
          customClass="retail-region-editable-cwf-lt">
          <div className={baseClasses('input-wrapper')}>
            <APSelect
              name="theme"
              label={this.context.t('Color Theme')}
              customClass={baseClasses()}
              options={this.getThemeOptions()}
            />
          </div>
        </ContentWithFooterLayoutTemplate>
      </div>
    );
  }
}

ThemeFormEditable.propTypes = {
  goToNextStep: PropTypes.func.isRequired
};

ThemeFormEditable.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ThemeFormEditable;



