import { change } from 'redux-form';
import { setUserConfig, updateUserConfig } from './userActions';
import { showSnack } from './snackbarActions';
import { formatErrorText } from '../../helpers/errorTextFormatter';

export const goToProfileFormStep = step => dispatch =>
  dispatch(change('userProfileForm', 'step', step));

export const submitUserProfileForm = () => async (dispatch, getState) => {
  const {
    form,
    user: { userInfo }
  } = getState();
  const { values } = form.userProfileForm;

  const UserId = parseInt(userInfo.UserId, 10);
  const Config = [
    { Value: values.fname, Key: 'fname' },
    { Value: values.lname, Key: 'lname' },
    { Value: values.email, Key: 'email' }
  ];
  const getUserConfig = false;

  const response = await dispatch(
    setUserConfig(UserId, values.username, Config, getUserConfig)
  );

  if (!response.result) {
    dispatch(
      showSnack({
        id: 'SET_USER_CONFIG_ERROR',
        text: formatErrorText(response),
        type: 'warning'
      })
    );
    return;
  }

  dispatch(updateUserConfig(Config));

  // context.t('Your profile has been successfully updated')
  dispatch(
    showSnack({
      id: 'SET_USER_CONFIG_SUCCESS',
      text: 'Your profile has been successfully updated',
      type: 'success'
    })
  );
};



