import React from 'react';
import RetailUserProfileStaticComponent from './RetailUserProfileStaticComponent';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import config from '../../../config';

import './RetailProfileFormComponent.css';

const classes = getBEMClasses('retail-profile-form');

const RetailProfileFormComponent = props => {
  const showUsernameField = !config.SignupForm.useEmailAsUsername;

  return (
    <div className={classes()}>
      <div className={classes('container')}>
        <RetailUserProfileStaticComponent
          showUsernameField={showUsernameField}
          userInfo={{
            email: props.formObj.email,
            username: props.formObj.username
          }}
        />
      </div>
    </div>
  );
};

export default RetailProfileFormComponent;



