import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';

import './ContentWithFooterLayoutTemplate.css';

/**
 * This is layout for following case:
 *  ___________________________
 * |                           |
 * | {content}                 |
 * |                           |
 * |___________________________|
 * | {footer}                  |
 * |___________________________|
 *
 * Note: footer is always attached to bottom
 */
const ContentWithFooterLayoutTemplate = props => {
  const { children, customClass, classModifiers, footer } = props;

  const classes = getBEMClasses('content-with-footer-lt', customClass);

  return (
    <div className={classes('layout-template', classModifiers)}>
      <div className={classes('content', classModifiers)}>{children}</div>
      <div className={classes('footer', classModifiers)}>{footer}</div>
    </div>
  );
};

ContentWithFooterLayoutTemplate.propTypes = {
  children: PropTypes.element.isRequired,
  footer: PropTypes.element.isRequired,
  customClass: PropTypes.string,
  classModifiers: PropTypes.arrayOf(PropTypes.string)
};

ContentWithFooterLayoutTemplate.defaultProps = {
  customClass: 'custom-retail-widget'
};

export default ContentWithFooterLayoutTemplate;



