import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import ResetPasswordConfirmComponent from './ResetPasswordConfirmComponent';
import Modal from '../../common/Modal/Modal';
import APButton from '../../common/APButton';

import './ResetPasswordFormComponent.css';
import ResetPasswordRequestFormContainerFactory from '../../ResetPasswordRequestForm/ResetPasswordRequestFormContainerFactory';

const classes = getBEMClasses('reset-password-form');

const ResetPasswordConfirmContainer = ResetPasswordRequestFormContainerFactory(
  ResetPasswordConfirmComponent
);

class ResetPasswordComponent extends React.Component {
  constructor() {
    super();
    this.state = { openModal: false };
  }

  toggleModal = () => this.setState({ openModal: !this.state.openModal });

  render() {
    const { openModal } = this.state;

    return (
      <React.Fragment>
        <div className={classes()}>
          <div className={classes('text-info')}>
            {this.context.t(
              'To change your password, we will send you an email that will allow you to access the Reset Password feature. Click the button below to receive the email.'
            )}
          </div>

          <div className={classes('button-container')}>
            <APButton
              type="button"
              customClass={classes()}
              onClick={this.toggleModal}>
              {this.context.t('Reset Your Password')}
            </APButton>
          </div>
        </div>
        {openModal && (
          <Modal
            isOpen={openModal}
            title={this.context.t('Reset Password')}
            onCancel={this.toggleModal}
            close={this.toggleModal}
            customClass={classes('ap-modal')}>
            <ResetPasswordConfirmContainer {...this.props} />
          </Modal>
        )}
      </React.Fragment>
    );
  }
}

ResetPasswordComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ResetPasswordComponent;



