import React from 'react';
import PropTypes from 'prop-types';
import ThemeConfirmComponent from './ThemeConfirmComponent';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import ThemeFormComponent from './ThemeFormComponent';
import APStepsComponent from '../../common/APStepsComponent';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import APButton from '../../common/APButton';
import Modal from '../../common/Modal/Modal';

import './ThemeSettingsComponent.css';

const themeClasses = getBEMClasses('theme-settings');

class ThemeSettingsComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };
  constructor() {
    super();
    this.state = { openModal: false };
  }

  toggleModal = () => this.setState({ openModal: !this.state.openModal });

  submitForm = () => {
    const { formValues, setUserConfig, UserId, UserName } = this.props;
    const storedValues = JSON.parse(localStorage.getItem('apexTemplate')) || {};
    const config = [{ Key: 'theme', Value: formValues.theme }];

    localStorage.setItem(
      'apexTemplate',
      JSON.stringify({ ...storedValues, ...formValues })
    );

    setUserConfig({ UserId, UserName, config });
    this.toggleModal();
  };

  getChildProps = () => ({
    doSubmit: this.submitForm,
    ...this.props
  });

  render() {
    const { formValues } = this.props;
    const { openModal } = this.state;

    return (
      <div className={themeClasses('main')}>
        <div className={themeClasses('body')}>
          <div className={themeClasses('row')}>
            <APLabelWithText
              label={this.context.t('Color Theme')}
              text={formValues.theme}
              customClass={themeClasses()}
            />
          </div>

          <div className={themeClasses('button-container')}>
            <APButton customClass={themeClasses()} onClick={this.toggleModal}>
              {this.context.t('Edit')}
            </APButton>
          </div>
          {openModal && (
            <Modal
              isOpen={openModal}
              title={this.context.t('Edit Theme')}
              onCancel={this.toggleModal}
              close={this.toggleModal}
              customClass={themeClasses('ap-modal')}>
              <APStepsComponent
                childComponents={[ThemeFormComponent, ThemeConfirmComponent]}
                {...this.getChildProps()}
              />
            </Modal>
          )}
        </div>
      </div>
    );
  }
}

export default ThemeSettingsComponent;



