import React from 'react';
import PropTypes from 'prop-types';
import RegionFormComponent from './RegionFormComponent';
import RegionConfirmComponent from './RegionConfirmComponent';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import APButton from '../../common/APButton';
import Modal from '../../common/Modal/Modal';
import APStepsComponent from '../../common/APStepsComponent';

import './RegionSettingsComponent.css';

const classes = getBEMClasses('region-settings');

class RegionSettingsComponent extends React.Component {
  constructor() {
    super();
    this.state = { openModal: false };
  }

  toggleModal = () => this.setState({ openModal: !this.state.openModal });

  onSubmit = () => {
    this.toggleModal();
    this.props.doSubmit();
  };

  render() {
    const {
      languageNames,
      formObj: { language },
      baseCurrency
    } = this.props;
    const { openModal } = this.state;

    return (
      <React.Fragment>
        <form className={classes()}>
          <div className={classes('row')}>
            <APLabelWithText
              label={this.context.t('Language')}
              text={languageNames[language]}
              customClass={classes()}
            />
          </div>

          <div className={classes('row')}>
            <APLabelWithText
              label={this.context.t('Display Currency')}
              text={baseCurrency}
              customClass={classes()}
            />
          </div>

          <div className={classes('button-container')}>
            <APButton
              customClass={classes()}
              disabled={openModal}
              onClick={this.toggleModal}>
              {this.context.t('Edit')}
            </APButton>
          </div>
          {openModal && (
            <Modal
              isOpen={openModal}
              title={this.context.t('Edit Language')}
              onCancel={this.toggleModal}
              close={this.toggleModal}
              customClass={classes('ap-modal')}>
              <APStepsComponent
                childComponents={[RegionFormComponent, RegionConfirmComponent]}
                {...this.props}
                doSubmit={this.onSubmit}
              />
            </Modal>
          )}
        </form>
      </React.Fragment>
    );
  }
}

RegionSettingsComponent.propTypes = {
  doSubmit: PropTypes.func.isRequired,
  editIsDisabled: PropTypes.bool
};

RegionSettingsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RegionSettingsComponent;



