import React from 'react';
import PropTypes from 'prop-types';
import APSelect from '../../common/APSelect/APSelect';
import APButton from '../../common/APButton';
import ContentWithFooterLayoutTemplate from '../../common/LayoutTemplates/ContentWithFooterLayoutTemplate/ContentWithFooterLayoutTemplate';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RegionFormComponent.css';

const regionFormClasses = getBEMClasses('region-form');

const RegionForm = (props, context) => {
  const {
    goToNextStep,
    translations,
    languageNames,
    productsOptions,
    isMisconfigured
  } = props;

  let languageOptions = [{ value: 'en', label: 'English' }];

  if (typeof translations === 'object') {
    const availableLangs = Object.keys(translations).filter(
      i => i !== 'options'
    );
    if (availableLangs.length) {
      languageOptions = availableLangs.map(value => ({
        value,
        label: languageNames[value]
      }));
    }
  }

  const footer = (
    <APButton customClass={regionFormClasses()} onClick={() => goToNextStep()}>
      {context.t('Next')}
    </APButton>
  );

  return (
    <div className={regionFormClasses()}>
      <ContentWithFooterLayoutTemplate
        footer={footer}
        customClass="retail-region-editable-cwf-lt">
        <React.Fragment>
          <div className={regionFormClasses('input-wrapper')}>
            <APSelect
              name="language"
              label={context.t('Language')}
              customClass={regionFormClasses()}
              options={languageOptions}
            />
          </div>
          <div className={regionFormClasses('input-wrapper')}>
            {isMisconfigured && (
              <p>
                {context.t(
                  'Base currency misconfigured. Contact the site’s operators.'
                )}
              </p>
            )}
            {!isMisconfigured &&
              productsOptions &&
              productsOptions.length > 1 && (
                <APSelect
                  name="baseCurrency"
                  customClass={regionFormClasses()}
                  label={context.t('Base Currency')}
                  options={[
                    { value: '', label: 'Select a currency' },
                    ...productsOptions
                  ]}
                />
              )}
          </div>
        </React.Fragment>
      </ContentWithFooterLayoutTemplate>
    </div>
  );
};

RegionForm.propTypes = {
  languageNames: PropTypes.object.isRequired,
  translations: PropTypes.object.isRequired,
  goToStep: PropTypes.func.isRequired
};

RegionForm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RegionForm;



