import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { submitResetPasswordForm } from '../../redux/actions/userActions';
import { goToResetPasswordFormStep } from '../../redux/actions/userActions';

const ResetPasswordRequestFormContainerFactory = Component => {
  const ResetPasswordRequestFormContainer = reduxForm({
    form: 'resetPasswordRequest',
    destroyOnUnmount: false,
    onSubmit: (payload, dispatch) => dispatch(submitResetPasswordForm(payload))
  })(Component);

  return connect(
    mapStateToProps,
    mapDispatchToProps
  )(ResetPasswordRequestFormContainer);
};

const mapStateToProps = state => {
  const { form } = state;

  return {
    formObj: form.resetPasswordRequest ? form.resetPasswordRequest.values : {}
  };
};

const mapDispatchToProps = {
  goToStep: goToResetPasswordFormStep
};

export default ResetPasswordRequestFormContainerFactory;



