import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import RetailProfileFormComponent from './RetailProfileFormComponent';
import { isProfileSectionEditingDisabled } from '../../../redux/selectors/userSelectors';
import {
  submitUserProfileForm,
  goToProfileFormStep
} from '../../../redux/actions/userProfileFormActions';

const RetailProfileForm = reduxForm({
  form: 'userProfileForm',
  enableReinitialize: true,
  onSubmit: (payload, dispatch) => dispatch(submitUserProfileForm(payload))
})(RetailProfileFormComponent);

const mapStateToProps = state => {
  const {
    form,
    user: { userInfo }
  } = state;

  const editIsDisabled = isProfileSectionEditingDisabled(state);

  return {
    initialValues: {
      username: userInfo.UserName,
      email: userInfo.Email
    },
    formObj: form.userProfileForm ? form.userProfileForm.values : {},
    editIsDisabled
  };
};

const mapDispatchToProps = {
  goToStep: goToProfileFormStep
};

const RetailProfileFormContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailProfileForm);

export default RetailProfileFormContainer;



