import React from 'react';
import PropTypes from 'prop-types';
import APLabelWithText from '../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailUserProfileStaticComponent.css';

const classes = getBEMClasses('retail-user-profile-form-static');

const RetailUserProfileStaticComponent = (props, context) => {
  const { showUsernameField, userInfo } = props;

  return (
    <div className={classes()}>
      {showUsernameField && (
        <div className={classes('row')}>
          <APLabelWithText
            label={context.t('Username')}
            text={userInfo.username}
            customClass={classes()}
          />
        </div>
      )}
      <div className={classes('row')}>
        <APLabelWithText
          label={context.t('Email')}
          text={userInfo.email}
          customClass={classes()}
        />
      </div>
    </div>
  );
};

RetailUserProfileStaticComponent.propTypes = {
  userInfo: PropTypes.object,
  showUsernameField: PropTypes.bool.isRequired
};

RetailUserProfileStaticComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailUserProfileStaticComponent;



